.Settings {
	cursor: pointer;
	border: none;
	background: none;
	outline: none;
	padding: 6px;
	position: relative;
	background-color: transparent;

	&:hover {
		animation: jump 1s ease-in-out;
		animation: rotate 0.5s ease-in-out;
	}
}

@keyframes jump {
	0% {
		transform: translateY(0);
	}
	50% {
		transform: translateY(-30px);
	}
	100% {
		transform: translateY(0);
	}
}

@keyframes rotate {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
